import {Component, OnInit} from '@angular/core';
import {FormGroup, FormControl,Validators} from "@angular/forms";
import {dataService} from "./dataservice/data.service";
@Component({
  selector: 'login-root',
  templateUrl: './login.component.html'

})
export class  LoginComponent implements OnInit
{
private
  loginform:FormGroup;

  constructor(private data:dataService)
  {

  }
  ngOnInit()
  {
    this.loginform = new FormGroup({
      'registerno': new FormControl('', [Validators.required, Validators.pattern("^[0-9]*$"),Validators.minLength(9)]),
      'password': new FormControl('', [Validators.required, Validators.pattern("^[0-9]*$"),Validators.minLength(8)])

    })
  }
public
  onLoginSubmit()
  {
    console.log("asdfasdfas")
    this.data.onsubmit(this.loginform.value)
  }
}



