
import {Component, OnInit, AfterViewInit} from '@angular/core';

import {dataService} from "./dataservice/data.service";

import {Router} from "@angular/router";
declare let $:any
@Component({
  selector: 'home-root',
  templateUrl: './home.component.html'

})
export class HomeComponent implements OnInit,AfterViewInit {
public datas :any;
public count:number = 0;
public grps:number = 0;
public error:boolean = false;
public electiveer:boolean = false;
  constructor(private data:dataService,private router:Router){

  }

  ngOnInit(){
   this.data.getdata();


  }
  ngAfterViewInit(){
    setInterval( ()=> {
      this.data.change = false
    },5000)
  }
  public saves(){
   let g= 13
    console.log()
    if(!this.data.studetails.section) {
      let section = $('input[name="secselection"]:checked').val();

      if (section == 'A') {
        g = 0
      }
      else if (section == 'B') {
        g = 1
      }
      else if (section == 'C') {
        g = 2
      }
      else if (section == 'D') {
        g = 3
      }
      else {
        this.error = true
        setTimeout(()=>{
          this.error = false
          this.electiveer = false
        },5000)
      }

    }
    let arr= [],arr1 = [];
let size = 0
    for(let i = 0;i<this.grps;i++)
    {
      let st = 'input[name=' +"elecselection" + i+ ']:checked';

    if($(st).attr("data-det") == undefined)
    {
      size = size + 1
    }
      arr1.push($(st).attr("data-det"))
      arr.push($(st).val())
    }

  console.log(size)
    if(size == this.grps && !this.error && !this.data.studetails.section)
    {
      console.log("updating")
      this.data.updates(g);
    }
    else {
      if(size == 0 && !this.error) {
        this.data.update(g, arr, arr1);
      }
      else {
        this.electiveer = true
        setTimeout(()=>{
          this.electiveer = false
        },5000)
      }
    }

  }
  public check(){
    if(this.data.secdet && this.data.change)
    {
      let str;
      let i
        if(this.data.studetails.section)
        {
          str = '<div class="alert alert-success" role="alert"><strong>Well done!</strong> You have  successfully selected '+ this.data.studetails.sectionname+'.</div>'
        }
        else
        {
          str = '<table class="table-sm table" style="text-align:center;"><thead><tr><th>S.no</th><th style="text-align: center;">Section Name</th><th style="text-align: center;">Remaining</th><th style="text-align: center;">Choose</th></tr></thead><tbody>';
           i = 0;
          this.data.secdet.section.forEach(function (datae) {
            str = str + '<tr><th scope="row">' + (i + 1) + '</th>';
            i = i + 1
            str = str + '<td>' + datae.name + '</td>';
            str = str + '<td>' + datae.number + '</td>';
            if(datae.number == 0 )
            {
              str = str + '<td>Filled</td>'

            }
            else {
              str = str + "<td>" + '<div   class="form-check text-center"><label class="form-check-label"><input class="form-check-input secclass" type="radio" name="secselection" id="sections' + datae.name + '" value="' + datae.name + '"></label></div></tr>'

            }
          })
          str = str + "</tbody></table>";
        }
      $('.secselect').html(str);
      if(this.data.studetails.elective)
      {
        str = '<div style="margin:2%" class="alert alert-success" role="alert"><strong>Well done!</strong> You have  successfully selected '

        this.data.studetails.electivedet.forEach(function (ed) {
        str = str + "<b>---" + ed + "----</b>"
        })

        str = str + '.</div>'

      }
      else {
        str = '';

        console.log(this.data.secdet.elective)
        let grp = 0
        let redd = 236;
        let gree = 232;
        let bll = 232;
        this.data.secdet.elective.forEach(function (datae) {
          console.log(datae)
          str = str + '<table class="table table-responsive" style="padding:2px;background-color:rgb('+redd+","+gree+','+bll +');text-align: center;margin-bottom: 0;"><thead><tr><th>S.no</th><th style="text-align: center;">Subject Code</th><th style="text-align: center;">Subject Name</th><th style="text-align: center;">Details</th></tr></thead><tbody>';

          i = 0
          datae.forEach(function (er) {
            str = str + '<tr><th scope="row">' + (i + 1) + '</th>';
            i = i + 1
            str = str + '<td style="vertical-align: middle">' + er.subjectcode + '</td>';
            str = str + '<td style="vertical-align: middle">' + er.name + '</td>';
            str = str + '<td><table class="table-sm table table-responsive" style="background-color:rgb('+redd+","+gree+','+bll +');text-align: center;margin: 0"><thead><tr><th style="text-align: center;">Staff Name</th><th style="text-align: center;">Remaining</th><th style="text-align: center;">Choose</th></tr></thead><tbody>';
            let seee = 0
            er.teachers.forEach(function (ee) {

              str = str + '<td style="width:80%;">' + ee.name + '</td>';
              str = str + '<td>' + ee.num + '</td>';
              if (ee.num == 0) {
                str = str + '<td>Filled</td></tr>'

              }
              else {
                str = str + "<td>" + '  <div class="form-check text-center"><label class="form-check-label"><input class="form-check-input elecclass" type="radio" name="elecselection' + grp + '" id="electi' + ee.id + seee + '" value="' + ee.name + '" data-det="' + er.subjectcode + '"></label></div></tr>';
              }
              seee = seee + 1
            })
            str = str + '</tbody></table></td></tr>'

          })

          gree = gree -16;
          bll = bll - 16;
          str = str + "</tbody></table>"
          grp = grp + 1
        })
        this.grps = grp
        console.log(this.grps)
      }
          $('.electiveselect').html(str)

      console.log(localStorage.getItem("section"))
if(localStorage.getItem("section"))
{
  let or = "input[value=" + localStorage.getItem("section") + "]"
  $(or).prop("checked",true)


}
      for(let i = 0;i<this.grps;i++) {
        let str = 'elective'+i
        let st = 'input[value="' + localStorage.getItem(str)+'"]';

        if(localStorage.getItem(str))
        {
          $(st).prop("checked",true)
        }


      }
$('.secclass').change(function () {
  let section = $('input[name="secselection"]:checked').val();
  localStorage.setItem("section",section)
})
      $('.elecclass').change( ()=> {
        console.log(this.grps)
        for(let i = 0;i<this.grps;i++) {
          let st = 'input[name=' + "elecselection" + i + ']:checked';
          let str = 'elective'+i
          localStorage.setItem(str,$(st).val())

        }
      })



this.data.change = false;

      return true
    }
    else {
      return false
    }
  }
  public logout()
  {

    localStorage.clear();
    this.router.navigate(['../login'])
  }
}



