import {Injectable, OnInit} from '@angular/core'
import {Router} from '@angular/router'
declare let firebase:any
@Injectable()
export class dataService implements OnInit {
  public rev:boolean = false;
public studetails:any;
public secdet:any = {} ;
public change:boolean = false;
public key:any;
public stukey :any;
public getbacksec:boolean = false;
public getbackele:boolean = false;
  constructor(private router:Router) {

  }

  ngOnInit() {
this.secdet = {}
  }
////////////////////////////////////////////
  public updates(sec){
    console.log(sec)
    firebase.database().ref('sections/'+this.key+"/section/"+sec).child("number").transaction((search,err)=> {

      if (err) throw err
      console.log(search)
      if(search > 0)
      {

        search  =  search - 1;
        firebase.database().ref('details/'+ this.stukey).update({
          'section' :true,
          'sectionname':String.fromCharCode(sec+65),

        })
        this.getbacksec = false;
      }
      else {
        this.getbacksec = true
      }
      return search;
    })






  }
  ////////////////////////////////////////////
  public update(sec,elec,ar){

    if(sec < 5) {
      console.log("asdfasdf")
      firebase.database().ref('sections/' + this.key + "/section/" + sec).child("number").transaction((search, err)=> {
        if (err) throw err
        console.log(search)
        if (search > 0) {
          search = search - 1;
        }
        return search;
      })
    }
    console.log(ar.length)
    for(let i=0;i<ar.length;i++) {
      console.log(ar[i])
      firebase.database().ref().child('sections/'+this.key+'/elective').child(i+"/").orderByChild("subjectcode").equalTo(ar[i]).once('value',(snapshot)=> {

        let ss = Object.keys(snapshot.val())[0];
        firebase.database().ref().child('sections/'+this.key+'/elective').child(i+"/"+ss+"/teachers").orderByChild("name").equalTo(elec[i]).once('value',(snapshot)=> {
          firebase.database().ref('sections/'+this.key+'/elective').child(i+"/"+ss+"/teachers/"+Object.keys(snapshot.val())[0]+"/").child("num").transaction((snapshot)=> {
            console.log(snapshot)
              if(snapshot > 0)
              {
                this.getbackele = false;
                snapshot = snapshot - 1
                if(sec < 5)
                {
                  console.log(sec)
                  firebase.database().ref('details/'+ this.stukey).update({
                    'section' :true,
                    'sectionname':String.fromCharCode(sec+65),
                    'elective' :true,
                    'electivedet':{
                      'names' :ar,
                      'teach' :elec

                    }
                  })
                }
                else {
                  firebase.database().ref('details/'+ this.stukey).update({
                    'section' :true,
                    'elective' :true,
                    'electivedet':{
                      'names' :ar,
                      'teach' :elec

                    }
                  })
                }
              }
            else {
                this.getbackele = true
              }
              return snapshot
          })
        })

        })
    }


  }

  ///////////////////////////////////////////////

  public getdata():any
  {
    firebase.database().ref().child('sections').orderByChild('year').equalTo(this.studetails.branch + this.studetails.year).on('value',(snapshot:any)=> {
     console.log("this");
     this.key = Object.keys(snapshot.val())[0];

      this.secdet =  snapshot.val()[snapshot.val().length - 1];

      this.change = true;
    })

    }
 public onsubmit(s:any){
   // localStorage.setItem("registerno", s.registerno);
   let reg = s.registerno

   firebase.database().ref().child('details').orderByChild('registerno').equalTo(reg).on('value',(snapshot:any)=> {

    let det = snapshot.val()

this.stukey = Object.keys(snapshot.val())[0];
det = Object.keys(det).map((k)=>{
  return det[k]
})
det = det[0]


     if(det.password == s.password) {
       localStorage.setItem('registerno', det.registerno);
       this.studetails = {
         registerno : det.registerno,
     year :det.year,
         branch : det.branches,
         name:det.Name,
         semester:det.semester,
         section:det.section,
         elective:det.elective


       }
       if(det.electivedet.names != '')
       {
         this.studetails.electivedet = det.electivedet.names
       }
       if(det.sectionname != undefined)
       {
         this.studetails.sectionname = det.sectionname;
       }
       this.change = true
       console.log(this.studetails)
       this.router.navigate(['../home'])

     }
     else {
       this.rev = true
     }

   });
   }
}



