import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{

  constructor(){
    window.onbeforeunload = function() {
      localStorage.clear();
      return '';
    };
    window.onunload = function () {
      localStorage.clear();
      return '';
    }
  }
  ngOnInit(){

  }

}



